import { Grid } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import { ButtonPrimary } from './../../components/ButtonPrimary/ButtonPrimary'
import CreateIcon from '@material-ui/icons/Create'
import { Modal, Table } from '../../components'
import { TCitiesAndStreetsList } from './CitiesAndStreetsListTypes'
import { InputVsLabel } from './../../components/InputVsLabel/InputVsLabel'

function createData(name: string) {
  return { name }
}

const rows = [
  createData('–£–ª—å—è–Ω–æ–≤—Å–∫'),
  createData('–ú–æ—Å–∫–≤–∞'),
  createData('–°–∞–º–∞—Ä–∞'),
  createData('–ü–∏—Ç–µ—Ä'),
  createData('–í–ª–∞–¥–∏–∫'),
]

function createDataStreets(dote: string, city: string, street: string) {
  return { dote, city, street }
}

const rowsStreets = [
  createDataStreets('dote-–£–ª—å—è–Ω–æ–≤—Å–∫', 'city-–£–ª—å—è–Ω–æ–≤—Å–∫', '–£–ª—å—è–Ω–æ–≤—Å–∫'),
  createDataStreets('dote-–ú–æ—Å–∫–≤–∞', 'city-–ú–æ—Å–∫–≤–∞', '–ú–æ—Å–∫–≤–∞'),
  createDataStreets('dote-–°–∞–º–∞—Ä–∞', 'city-–°–∞–º–∞—Ä–∞', '–°–∞–º–∞—Ä–∞'),
  createDataStreets('dote-–ü–∏—Ç–µ—Ä', 'city-–ü–∏—Ç–µ—Ä', '–ü–∏—Ç–µ—Ä'),
  createDataStreets('dote-–í–ª–∞–¥–∏–∫', 'city-–í–ª–∞–¥–∏–∫', '–í–ª–∞–¥–∏–∫'),
]

export const CitiesAndStreetsListView: React.FC<TCitiesAndStreetsList> = ({
  isOpenCity,
  isOpenStreet,
  isEditStreet,
  isEditCity,
  handleOpenModalCity,
  handleCloseModalCity,
  handleOpenModalStreet,
  handleCloseModalStreet,
  handleEditStreet,
  handleEditCity,
}) => {
  return (
    <Grid container className="gridContainer">
      <Grid item xs={12} sm={6} className={'gridItem '}>
        <h2 className="admin-page-title">–ì–æ—Ä–æ–¥–∞</h2>
        <div className="content-wrap withOutHeaderFooter">
          <Table handlerOpenAdd={handleOpenModalCity} tableHeadData={['–ì–æ—Ä–æ–¥']}>
            {rows.map((row) => {
              return (
                <TableRow>
                  <TableCell component="th" scope="row">
                    <ButtonPrimary
                      onClick={handleEditCity}
                      className={'buttonInTable'}
                    >
                      <CreateIcon />
                    </ButtonPrimary>
                  </TableCell>
                  <TableCell>{row.name}</TableCell>
                </TableRow>
              )
            })}
          </Table>
        </div>
      </Grid>
      <Grid item xs={12} sm={6} className={'gridItem'}>
        <h2 className="admin-page-title">–¢–æ—á–∫–∏ –≤—ã–¥–∞—á–∏</h2>
        <div className="content-wrap withOutHeaderFooter">
          <Table
            handlerOpenAdd={handleOpenModalStreet}
            tableHeadData={['–ì–æ—Ä–æ–¥', '–£–ª–∏—Ü–∞', '–¢–æ—á–∫–∞ –≤—ã–¥–∞—á–∏']}
          >
            {rowsStreets.map((row) => {
              return (
                <TableRow>
                  <TableCell component="th" scope="row">
                    <ButtonPrimary
                      onClick={handleEditStreet}
                      className={'buttonInTable'}
                    >
                      <CreateIcon />
                    </ButtonPrimary>
                  </TableCell>
                  <TableCell>{row.city}</TableCell>
                  <TableCell>{row.street}</TableCell>
                  <TableCell>{row.dote}</TableCell>
                </TableRow>
              )
            })}
          </Table>
        </div>
      </Grid>

      <Modal
        open={isOpenCity}
        onClose={handleCloseModalCity}
        title={isEditCity ? '–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å' : '–î–æ–±–∞–≤–∏—Ç—å' + ' –≥–æ—Ä–æ–¥'}
        buttonClick={() => console.log('r')}
        buttonTitle="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      >
        <InputVsLabel
          id="city"
          label="–í–≤–µ–¥–∏—Ç–µ –≥–æ—Ä–æ–¥"
          onChange={() => console.log('k')}
        />
      </Modal>
      <Modal
        open={isOpenStreet}
        onClose={handleCloseModalStreet}
        title={isEditStreet ? '–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å' : '–î–æ–±–∞–≤–∏—Ç—å' + ' —Ç–æ—á–∫—É –≤—ã–¥–∞—á–∏'}
        buttonClick={() => console.log('r')}
        buttonTitle="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      >
        <InputVsLabel
          id="street"
          label="–í–≤–µ–¥–∏—Ç–µ —Ç–æ—á–∫—É –≤—ã–¥–∞—á–∏"
          onChange={() => console.log('k')}
        />
        <InputVsLabel
          id="street"
          label="–í–≤–µ–¥–∏—Ç–µ –≥–æ—Ä–æ–¥"
          onChange={() => console.log('k')}
        />
        <InputVsLabel
          id="street"
          label="–í–≤–µ–¥–∏—Ç–µ –∞–¥—Ä–µ—Å"
          onChange={() => console.log('k')}
        />
      </Modal>
    </Grid>
  )
}
